import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DishPreparationComponent } from './dish-preparation.component';

const routes: Routes = [
  {
    path: '',
    component: DishPreparationComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DishPreparationRoutingModule {}
