import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { DishPreparationService } from './dish-preparation.service';
import { Observable } from 'rxjs';
import { startWith } from 'rxjs/internal/operators/startWith';
import { map } from 'rxjs/internal/operators/map';
import { CommonService } from '../../../shared/services/common.service';
import { MealTypes, DishPreparePageData, Chef, IngedientAndQnty, Media, Nutrition, Dish } from './dish-interfaces';
import { DialogueService } from 'src/app/shared/commmon/dialogue/dialogue.service';
import { MatDialog } from '@angular/material';
import { DialogueComponent } from 'src/app/shared/commmon/dialogue/dialogue.component';
@Component({
  selector: 'app-dish-preparation',
  templateUrl: './dish-preparation.component.html',
  styleUrls: ['./dish-preparation.component.scss']
})
export class DishPreparationComponent implements OnInit {

  MealTypes: MealTypes[] = [];
  Chef;
  DishPreparePageData: DishPreparePageData;
  chefNamesCtl = new FormControl();
  FilteredchefNames: Observable<Chef[]>;
  Chefs: Chef[] = [];
  ChefID = 0;
  FilteredFoodIngredients: Observable<IngedientAndQnty[]>;
  DietecMedia: Media[] = [];
  Nutritions: Nutrition[] = [];
  SelectedMealTypes: MealTypes[] = [];
  FoodIngredients: IngedientAndQnty[];
  SelectedIngredients = [];
  Quantity = '';
  response = '';
  Title = '';
  Description = '';
  chefNames = new FormControl();
  IngredientNameCtl = new FormControl();


  constructor(private service: DishPreparationService, private common: CommonService, private popup: DialogueService,
    private dialog: MatDialog) {
    this.GetFoodPreparePageData();
    this.GetAllFoodIngredients();
    this.FilterFoodItems();
  }
  // Filter food items
  FilterFoodItems() {
    this.FilteredchefNames = this.chefNamesCtl.valueChanges
      .pipe(
        startWith(''),
        map(chef => chef ? this._filterChefs(chef) : this.Chefs.slice())
      );
      this.FilteredFoodIngredients = this.IngredientNameCtl.valueChanges
      .pipe(
        startWith(''),
        map(chef => chef ? this._filterIngredients(chef) : [])
      );
  }
  // get page data
  GetFoodPreparePageData() {
    this.service.GetPrepareFoodPageData().subscribe(Response => {
      if (Response.Status === true) {
        this.DishPreparePageData = Response.Data;
        this.Chefs = this.DishPreparePageData.Chef;
        this.MealTypes = this.DishPreparePageData.MealType;
        this.Nutritions = this.DishPreparePageData.Nutritions;
      }
    });
  }
  // get all food ingredients
  GetAllFoodIngredients() {
    this.service.GetAllIngredients().subscribe(response => {
      this.FoodIngredients = response;
    });
  }
  // filter chef
  private _filterChefs(value: string): Chef[] {
    const filterValue = value;
    return this.Chefs.filter(chef => chef.ChefName.toLowerCase().indexOf(filterValue) === 0);
  }
  // chef display function
  ChefdisplayFn(chef?: Chef): string | undefined {
    this.ChefID = chef ? chef.ChefId : undefined;
    return chef ? chef.ChefName : undefined;
  }
  // filter ingredient function
  private _filterIngredients(value: string): IngedientAndQnty[] {
    const filterValue = value;
    return this.FoodIngredients.filter(food => food.FoodName.toLowerCase().indexOf(filterValue) === 0);
  }
  // ingredient display function
  IngredientdisplayFn(food?: IngedientAndQnty): string | undefined {
    return food ? food.FoodName : undefined;
  }
  // upload image files
  handleImageFileInput(files: FileList) {
    this.response = 'Please wait';
    if (files.length > 0) {
      this.common.DietecMediaUpload(files[0]).subscribe(Response => {if (Response.Status === true) {
        if (Response.Status === true) {
            this.DietecMedia.push({MediaType: 1, FileLocation: Response.FileName});
          }
      }});
    }
  }
  // delete image file
  DeleteImageFile(FileName) {
      this.DietecMedia.forEach((element, index) => {
        if (element.FileLocation === FileName) {
          this.DietecMedia.splice(index, 1);
        }
      });
  }
  // upload video files
  handleVideoFileInput(files: FileList) {
    console.log(files.length);
    if (files.length > 0) {
      this.common.DietecMediaUpload(files[0]).subscribe(Response => {if (Response.Status === true) {
          if (Response.Status === true) {
            this.DietecMedia.push({MediaType: 2, FileLocation: Response.FileName});
          }
      }});
    }

  }
  // add meal types to food
  AddMealType(event) {
  for (let index = 0; index < this.SelectedMealTypes.length; index++) {
    if (event.source.value === this.SelectedMealTypes[index].MealTypeId) {
        this.SelectedMealTypes.splice(index, 1);
    }
  }
  if (event.checked === true) {
      this.SelectedMealTypes.push({'MealTypeId': event.source.value, 'MealTypeName': ''});
    }
  }
  // save dish add dialogue and redirect to dashboard
  SaveDish() {
    const DishDetail: Dish = {
      Title: this.Title,
      Description: this.Description,
      DishNutrition: this.Nutritions,
      ChefId: this.chefNamesCtl.value.ChefId,
      DishMealTypes: this.MealTypes,
      DishMedia: this.DietecMedia,
      DishIngredients: this.SelectedIngredients
    };
    this.service.SaveDish(DishDetail).subscribe(Response => {if (Response.Status === true) {
      if (Response.Status === true) {
        this.openDialog();
        this.popup.SetData({Status: '', Message: 'Data Saved Successfully', Route: '/ab/diet'});
      }
    }});
  }
  // add ingredient to list
  AddIngredient() {
    const FoodDetail = this.IngredientNameCtl.value;
    this.SelectedIngredients.forEach((element, index) => {
      if (element.FoodId === FoodDetail.FoodId) {
        this.SelectedIngredients.splice(index, 1);
      }
    });
    const item = {'IngredientId': FoodDetail.FoodId, 'Quantity': this.Quantity, 'FoodName': FoodDetail.FoodName};
      if (this.Quantity !== ''  && FoodDetail.FoodId != null) {
      this.SelectedIngredients.push(item);
    }
    this.IngredientNameCtl.setValue('');
  }
  // remove ingredient from the list
  RemoveIngredient(FoodID) {
    this.SelectedIngredients.forEach((element, index) => {
      if (element.FoodId === FoodID) {
        this.SelectedIngredients.splice(index, 1);
      }
    });
  }
  // on init
  ngOnInit() {
  }
  // open dialogue
  openDialog(): void {
    const dialogRef = this.dialog.open(DialogueComponent, {
      width: '250px',
      disableClose: true
    });
  // fires when dialogue closed
    dialogRef.afterClosed().subscribe(result => {
      //
    });
  }


}
